<template>
  <div class="home">
    <h2>Best stock photo</h2>
    <div class="search__photo">
      <input placeholder="Search photos..." v-model="search" type="text">
    </div>
    <div id="photos" class="photos">
      <div class="photo__wrapper" v-infinite-scroll="getNewPage" infinite-scroll-disabled="show" infinite-scroll-distance="3000">
        <div v-for="(photo, index) in searchData" :key="index">
          <img :src="photo.urls.small"/>
        </div>
        <div class="photo__no" v-if="!searchData.length">No results were found.</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'Home',
  data () {
    return {
      limit: 1,
      search: ''
    }
  },
  computed: {
    ...mapGetters(['allGallery']),
    searchData () {
      const search = this.search
      if (search) {
        return this.allGallery.filter((item) => {
          return Object.keys(item).some((key) => {
            return String(item[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }
      return this.allGallery
    }
  },
  methods: {
    ...mapActions(['getGallery']),
    async getNewPage () {
      await this.getGallery(this.limit = this.limit += 1)
    }
  }
}
</script>

