import axios from 'axios'

export default {
  state: {
    photos: []
  },
  actions: {
    async getGallery (cxt, limit) {
      const res = await axios.get(
        'https://api.unsplash.com/photos/?client_id=WCeAz3goiocs9ghIGkWNKaE2Uh1jvu-U2vnk5ISEBF0&per_page=30&page=' + limit)
      const photos = await res.data
      cxt.commit('UPDATE_GALLERY', photos)
    }
  },
  mutations: {
    UPDATE_GALLERY (state, photos) {
      state.photos = [...state.photos, ...photos]
    }
  },
  getters: {
    allGallery (state) {
      return state.photos
    }
  }
}
